var qiniu=require('qiniu-js');
import uniGrid from '../../../../components/uni-grid/uni-grid.vue'
import myIssue  from '../../../../components/start/App.vue'
export default{
	data(){
		return{
				filesList:[],
				imgList: [],
				cur1:5,
				cur2:5,
				cur3:5,
				flag:false,
				repaitItem:'',
				files:[],
				summary:'',//报修内容
				detail1:'非常满意',
				detail2:'非常满意',
				detail3:'非常满意',
				orderID:''

		}
	},
	components:{
		myIssue,
		uniGrid
	},
	methods:{
		viewImg(){
			let array=[];
			this.repaitItem.files.forEach(item=>{
				if(item.postfix){
					array.push(item.url)
				}
			})
			console.log(array)
			uni.previewImage({
				urls: array
			})
			
		},
		//获得订单信息
		getOrderInfo(id){
			
			this.$ajax('ServiceOrder',{id:id},res=>{
				
				this.repaitItem=res
				console.log(this.repaitItem)
				if(res.files){
					res.files.forEach(item=>{
						if(item.postfix){
							this.filesList.push({
								
								image:item.url,
								text:''
							})
						}
						
						
					})
				}
				
			})
			// uni.request({
			// 	url:this.$store.state.url+'ServiceOrder',
			// 	data:{
			// 		id:id
			// 	},
			// 	success: (res) => {
			// 		if(!res.data.data.summary){
			// 			res.data.data.summary=''
			// 		}
			// 		this.repaitItem=res.data.data;
			// 	}
			// })
		},
	ChooseImage(event) {
		
		uni.chooseImage({
			count:9,
			sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
			sourceType: ['album'], //从相册选择
			success: (res) => {
				const tempFilePaths=res.tempFilePaths;
				if (this.imgList.length != 0) {
					this.imgList = this.imgList.concat(res.tempFilePaths)
				} else {
						this.imgList = res.tempFilePaths
				}
				uni.getStorage({
					key:'userInfo',
					success: (info) => {
					
						for(var i=0;i<res.tempFilePaths.length;i++){
							var  uploadTask=uni.uploadFile({
								url:this.$store.state.uploadHostUrl+this.token,
								filePath:tempFilePaths[i],
								name:'file',
								formData:{
									'x:type':this.$store.state.constants.serviceorder_file,
									'x:owner': info.data.owner,
									'x:creator': info.data.id,
								},
								success: (uploadFileRes) => {
									let res=JSON.parse(uploadFileRes.data);
									this.files.push(res.data);
								}
							});
							uploadTask.onProgressUpdate((res)=>{
								if(res.progress==100){
									uni.showToast({
										title:'上传成功',
										icon:'none'
									})
								}
							},(error)=>{
								uni.showToast({
									title:'上传失败',
									icon:'none'
								})
							})
						}
					}
				})
				
				
		
		
			}
		})
	},
		ViewImage(e) {
			uni.previewImage({
				urls: this.imgList,
				current: e
			});
		},
		DelImg(event) {
			uni.showModal({
				content: '确定删除？',
				cancelText: '取消',
				confirmText: '确定',
				success: res => {
					if (res.confirm) {
						this.imgList.splice(event, 1);
						this.$ajax('RemoveFiles',{
							id:this.files[event]
						},res=>{
							this.files.splice(event,1)
							uni.showToast({
								title:'删除成功',
								icon:'none'
							})
						})
						// uni.request({
						// 	url:this.$store.state.url+'RemoveFiles',
						// 	data:{
						// 		id:this.files[event],
						// 		owner:18
						// 	},
						// 	success: (res) => {
						// 		this.files.splice(event,1)
						// 		uni.showToast({
						// 			title:'删除成功',
						// 			icon:'none'
						// 		})
						// 	}
						// })
					}
				}
			})
		},
		//获得上传token
		getUploadToken(){
			uni.request({
				url:this.$store.state.url+'UploadToken',
				success: (res) => {
					this.token=res.data.data
		
				}
			})
		},
		selected(n,type){
			
			this.flag=true;
			if(type=='service'){
				this.cur1=n;
				if(n>=5){
					this.detail1='非常满意';
				}else if(n>=3){
					this.detail1='满意';
				}else if(n>=1){
					this.detail1='一般';
				}else {
					this.detail1='不满意';
				}
			}
			if(type=='timely'){
				this.cur2=n;
				if(n>=5){
					this.detail2='非常满意';
				}else if(n>=3){
					this.detail2='满意';
				}else if(n>=1){
					this.detail2='一般';
				}else {
					this.detail2='不满意';
				}
			}
			if(type=='quantity'){
				this.cur3=n;
				if(n>=5){
					this.detail3='非常满意';
				}else if(n>=3){
					this.detail3='满意';
				}else if(n>=1){
					this.detail3='一般';
				}else {
					this.detail3='不满意';
				}
			}
			
		},
		createComment(){
			this.$ajax('NewServiceOrderComments',{
				comment:this.summary,
				id:this.orderID,
				service:this.cur1,
				timely:this.cur2,
				quality:this.cur3,
				files:this.files?this.files.join(','):''
			},res=>{
				uni.showToast({
					title:'验收评价成功',
					icon:'none'
				});
				setTimeout(()=>{
					uni.navigateBack({
						delta: 1
					});
				},500)
			})
			// uni.request({
			// 	url:this.$store.state.url+'NewServiceOrderComments',
			// 	data:{
			// 		owner:18,
			// 		userId:43,
			// 		comment:this.summary,
			// 		id:this.orderID,
			// 		service:this.cur1,
			// 		timely:this.cur2,
			// 		quality:this.cur3,
			// 		files:this.files?this.files.join(','):''
			// 		
			// 	},
			// 	success: (res) => {
			// 		uni.showToast({
			// 			title:'验收评价成功',
			// 			icon:'none'
			// 		});
			// 		setTimeout(()=>{
			// 			uni.navigateBack({
			// 				delta: 1
			// 			});
			// 		},500)
			// 	}
			// })
		}
	},
	onLoad(option){
		console.log(option)
		this.getUploadToken();
		this.orderID=option.orderID;
		this.getOrderInfo(option.orderID);
		console.log(this.repaitItem)
	}
	
}
